import { css } from 'lit';

export default css`
  code[class*='language-'],
  pre[class*='language-'] {
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    tab-size: 2;

    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
  }

  /* Code blocks */
  pre[class*='language-'] {
    padding: 1em;
    margin: 0.5em 0;
    overflow: auto;
  }

  /* Inline code */
  :not(pre) > code[class*='language-'] {
    white-space: normal;
  }

  .token.comment,
  .token.block-comment,
  .token.prolog,
  .token.doctype,
  .token.cdata {
    color: var(--light-fg);
  }

  .token.punctuation {
    color: var(--fg);
  }

  .token.tag,
  .token.attr-name,
  .token.namespace,
  .token.deleted {
    color: var(--pink);
  }

  .token.function-name {
    color: var(--blue);
  }

  .token.boolean,
  .token.number,
  .token.function {
    color: var(--red);
  }

  .token.property,
  .token.class-name,
  .token.constant,
  .token.symbol {
    color: var(--code-property-color);
  }

  .token.selector,
  .token.important,
  .token.atrule,
  .token.keyword,
  .token.builtin {
    color: var(--code-keyword-color);
  }

  .token.string,
  .token.char,
  .token.attr-value,
  .token.regex,
  .token.variable {
    color: var(--green);
  }

  .token.operator,
  .token.entity,
  .token.url {
    color: var(--code-operator-color);
  }

  .token.important,
  .token.bold {
    font-weight: bold;
  }
  .token.italic {
    font-style: italic;
  }

  .token.entity {
    cursor: help;
  }

  .token.inserted {
    color: green;
  }
`;
